import { useState, useEffect } from "react";
import {
  Box,
  Container,
  Typography,
  Divider,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Switch,
} from "@mui/material";
import { CreditCard, Download } from "@mui/icons-material";
import SettingSection from "./components/SettingSection";
import AccountSection from "./components/AccountSection";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import Sidebar from "../Dashboard/components/Sidebar";
import PropTypes from "prop-types";

const SettingsPage=({ mobileOpen, handleDrawerToggle })=> {
  const [cards] = useState([
    { id: 1, last4: "4242", type: "Visa" },
    { id: 2, last4: "8888", type: "Mastercard" },
  ]);
  const navigate = useNavigate();
  const [notifications, setNotifications] = useState(true);
  const [darkMode, setDarkMode] = useState(false);
  const [twoFactor, setTwoFactor] = useState(false);
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [isInstallable, setIsInstallable] = useState(false);
  useState(false);





  useEffect(() => {
    const handleBeforeInstallPrompt = (e) => {
      e.preventDefault();
      setDeferredPrompt(e);
      setIsInstallable(true);
    };

    window.addEventListener("beforeinstallprompt", handleBeforeInstallPrompt);

    return () => {
      window.removeEventListener(
        "beforeinstallprompt",
        handleBeforeInstallPrompt
      );
    };
  }, []);

  const handleInstall = async () => {
    if (deferredPrompt) {
      deferredPrompt.prompt();
      const { outcome } = await deferredPrompt.userChoice;

      if (outcome === "accepted") {
        console.log("User accepted the install prompt");
        setIsInstallable(false);
      } else {
        console.log("User dismissed the install prompt");
      }

      setDeferredPrompt(null);
    }
  };

  const handleToggle = (setting) => {
    switch (setting) {
      case "notifications":
        setNotifications(!notifications);
        break;
      case "darkMode":
        setDarkMode(!darkMode);
        break;
      case "twoFactor":
        setTwoFactor(!twoFactor);
        break;
      default:
        break;
    }
  };

  return (
      <Box sx={{ display: 'flex' }}>
        <Sidebar mobileOpen={mobileOpen} handleDrawerToggle={handleDrawerToggle} />
   
    <Box
      sx={{
        minHeight: "100vh",
        display: "flex",
        alignItems: "flex-start",
        justifyContent: "center",
        padding: 2,
        width: "100%",
        maxWidth: "1200px",
        margin: "0 auto",
      }}
    >
      <Container maxWidth="md">
        <Typography
          variant="h4"
          component="h1"
          sx={{
            mb: 4,
            color: "primary.main",
            fontWeight: 700,
            letterSpacing: "0em",
            fontSize: { xs: "2rem", sm: "2.5rem", md: "3rem" },
            lineHeight: 1.2,
          }}
        >
          Settings
        </Typography>

        <AccountSection />
             
          <SettingSection title="App Settings">
            <List>
              <ListItem>
                <ListItemText
                  primary="Notifications"
                  secondary="Receive payment and security alerts"
                />

                <ListItemSecondaryAction>
                  <Switch
                    edge="end"
                    onChange={() => handleToggle("notifications")}
                    checked={notifications}
                  />
                </ListItemSecondaryAction>
              </ListItem>

              <Divider sx={{ backgroundColor: "rgba(255, 255, 255, 0.12)" }} />
             
              <Divider sx={{ backgroundColor: "rgba(255, 255, 255, 0.12)" }} />

              <ListItem>
                <ListItemText
                  primary="Two-Factor Authentication"
                  scondary="Add an extra layer of security"
                />

                <ListItemSecondaryAction>
                  <Switch
                    edge="end"
                    onChange={() => handleToggle("twoFactor")}
                    checked={twoFactor}
                  />
                </ListItemSecondaryAction>
              </ListItem>
            </List>
          </SettingSection>
        
        <SettingSection title="Cards">
          {cards.map((card) => (
            <Box
              key={card.id}
              sx={{ display: "flex", alignItems: "center", mb: 2 }}
            >
              <CreditCard sx={{ mr: 2 }} />

              <Typography>
                {card.type}
                
                {" ending in "}
                 
                 {card.last4}
              </Typography>
            </Box>
          ))}

          <Button
            variant="outlined"
            onClick={() => navigate("/dashboard/add-card")}
            sx={{
              color: "white",
              borderColor: "rgba(255, 255, 255, 0.1)",
              "&:hover": {
                borderColor: "#2DD4BF",
                backgroundColor: "rgba(45, 212, 191, 0.1)",
              },
              textTransform: "none",
              py: 1,
              mt: 2,
            }}
          >
            Add New Card
          </Button>
        </SettingSection>

        <SettingSection title="Download">
          <Button
            variant="outlined"
            fullWidth
            sx={{
              color: "white",
              borderColor: "rgba(255, 255, 255, 0.1)",
              "&:hover": {
                borderColor: "#2DD4BF",
                backgroundColor: "rgba(45,211,191,0.1)",
              },
              textTransform: "none",
              py: 2,
              mb: 2,
            }}
            href="/apk/app-debug.apk"
            download
            component="a"
          >
            {`Download`}
          </Button>

          {isInstallable && (
            <Button
              variant="outlined"
              fullWidth
              startIcon={<Download />}
              onClick={handleInstall}
              sx={{
                color: "#2DD4BF",
                borderColor: "#2DD4BF",
                "&:hover": {
                  backgroundColor: "rgba(45, 212, 191, 0.1)",
                  borderColor: "#2DD4BF",
                },
                textTransform: "none",
                py: 2,
              }}
            >
              Install PWA
            </Button>
          )}
        </SettingSection>
      </Container>
    </Box>

    
    </Box>  
    );
}

SettingsPage.propTypes = {
  mobileOpen: PropTypes.bool.isRequired,
  handleDrawerToggle: PropTypes.func.isRequired,
};
export default SettingsPage;