import { useState } from 'react';
import { Card, CardContent, Typography, Table, TableBody, TableCell, TableHead, TableRow, TablePagination } from '@mui/material';

const transactions = [
  {
   merchant: "Netflix",
    amount: "$15.99",
    date: "Mar 15, 2024",
    status: "Completed"
  },
{
    merchant: "Spotify",
    amount: "$9.99",
    date: "Mar 14, 2024",
    status: "Completed"
  },
  {
    merchant: "Amazon Prime",
    amount: "$14.99",
    date: "Mar 13, 2024",
    status: "Completed"
  },
  {
    merchant: "Apple Music",
    amount: "$10.99",
    date: "Mar 12, 2024",
    status: "Failed"
  },
  {
    
 merchant: "Disney+",
    amount: "$7.99",
    date: "Mar 11, 2024",
    status: "Pending"
  },
  // Add more transactions here to demonstrate pagination
  {
    merchant: "Uber",
    amount: "$25.50",
    date: "Mar 10, 2024",
    status: "Completed"
  },
  {
    merchant: "Starbucks",
    amount: "$4.75",
    date: "Mar 9, 2024",
    status: "Completed"
  },
  {
    merchant: "Amazon",
    amount: "$67.89",
    date: "Mar 8, 2024",
    status: "Completed"
  },
  {
    merchant: "Gym Membership",
    amount: "$49.99",
    date: "Mar 7, 2024",
    status: "Pending"
  },
  {
    merchant: "Phone Bill",
    amount: "$85.00",
    date: "Mar 6, 2024",
    status: "Completed"
  }
];

export default function PaymentHistory() {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <Card
      sx={{
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        border: '1px solid rgba(255, 255, 255, 0.1)',
        borderRadius: 2,
      }}
    >
      <CardContent>
        <Typography variant="h6" sx={{ color: 'white', mb: 3 }}>
          Payment History
        </Typography>

        <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)', mb: 3 }}>
          View all your recent transactions and their status.
        </Typography>

        <Table>
          <TableHead>
            <TableRow>
              <TableCell sx={{ color: 'rgba(255, 255, 255, 0.7)', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                Merchant
              </TableCell>

              <TableCell sx={{ color: 'rgba(255, 255, 255, 0.7)', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                Amount
              </TableCell>

              <TableCell sx={{ color: 'rgba(255, 255, 255, 0.7)', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                Date
              </TableCell>

              <TableCell sx={{ color: 'rgba(255, 255, 255, 0.7)', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                Status
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {transactions
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((transaction, index) => (
                <TableRow key={index}>
                  <TableCell sx={{ color: 'white', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                    {transaction.merchant}
                  </TableCell>

                  <TableCell sx={{ color: 'white', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                    {transaction.amount}
                  </TableCell>

                  <TableCell sx={{ color: 'white', borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                    {transaction.date}
                  </TableCell>

                  <TableCell
                    sx={{
                      borderBottom: '1px solid rgba(255, 255, 255, 0.1)',
                      color: transaction.status === 'Completed'
                        ? '#2DD4BF'
                        : transaction.status === 'Failed'
                        ? '#ef4444'
                        : 'rgba(255, 255, 255, 0.7)',
                    }}
                  >
                    {transaction.status}
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>

        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={transactions.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
          sx={{
            color: 'white',
            '.MuiTablePagination-selectLabel, .MuiTablePagination-displayedRows': {
              color: 'rgba(255, 255, 255, 0.7)',
            },
            '.MuiTablePagination-select': {
              color: 'white',
            },
            '.MuiTablePagination-selectIcon': {
              color: '#2DD4BF',
            },
            '.MuiTablePagination-actions': {
              color: '#2DD4BF',
            },
          }}
        />
      </CardContent>
    </Card>
  );
}

