  const [newPaymentDialogOpen, setNewPaymentDialogOpen] = useState(false);
  const [groupDialogOpen, setGroupDialogOpen] = useState(false);
  const [addPayeeDialogOpen, setAddPayeeDialogOpen] = useState(false);
  const [activeView, setActiveView] = useState('groups'); // 'contacts' or 'groups'

  const handleAddPayee = (newPayee) => {
    console.log('New payee added:', newPayee);
    // Here you would typically update your state or make an API call
  };

