import { Box, List, ListItem, ListItemIcon, ListItemText, Paper, Typography, Drawer, IconButton } from '@mui/material';
import { Home, CreditCard, DollarSign, X } from 'lucide-react';
import { useLocation, useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';

const menuItems = [
  { text: 'Home', icon: Home, path: '/dashboard' },
  { text: 'Payments', icon: DollarSign, path: '/dashboard/payments' },
  { text: 'Cards', icon: CreditCard, path: '/dashboard/manage-cards' },
];

const SidebarContent = ({ onClose }) => {
  const location = useLocation();
  const navigate = useNavigate();
  
  const handleNavigation = (path) => {
    navigate(path);
    onClose();
  };

  return (
    <Paper
      sx={{
        width: 240,
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        borderRadius: 0,
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      <Box sx={{ p: 3, borderBottom: '1px solid rgba(255, 255, 255, 0.1)', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Typography
          variant="h4"
          sx={{
            color: '#2DD4BF',
            fontWeight: 600,
            letterSpacing: '-0.02em',
            fontSize: { xs: "2.5rem", sm: "3rem", md: "4.5rem" },
          }}
          onClick={() => handleNavigation('/home')}
        >
          rife.
        </Typography>

        <IconButton
          onClick={onClose}
          sx={{
            display: { xs: 'block', sm: 'none' },
            color: '#2DD4BF',
          }}
        >
          <X size={24} />
        </IconButton>
      </Box>

      <List sx={{ flexGrow: 1, pt: 2 }}>
        {menuItems.map((item) => (
          <ListItem
            button
            key={item.text}
            onClick={() => handleNavigation(item.path)}
            sx={{
              color: 'white',
              '&:hover': {
                backgroundColor: 'rgba(45, 212, 191, 0.1)',
              },
              ...(location.pathname === item.path && {
                backgroundColor: 'rgba(45, 212, 191, 0.2)',
                '&:hover': {
                  backgroundColor: 'rgba(45, 212, 191, 0.3)',
                },
              }),
            }}
          >
            <ListItemIcon sx={{ color: '#2DD4BF' }}>
              <item.icon size={24} />
            </ListItemIcon>

            <ListItemText primary={item.text} />
          </ListItem>
        ))}
      </List>
    </Paper>
  );
};

SidebarContent.propTypes = {
  onClose: PropTypes.func.isRequired,
};

export default function Sidebar({ mobileOpen, handleDrawerToggle }) {
  return (
    <>
      {/* Desktop sidebar */}
      <Box
        component="nav"
        sx={{
          width: 240,
          flexShrink: 0,
          display: { xs: 'none', sm: 'block' },
          position: 'fixed',
          top: 0,
          left: 0,
          height: '100vh',
        }}
      >
        <SidebarContent onClose={() => {}} />
      </Box>

      {/* Mobile drawer */}
      <Drawer
        variant="temporary"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true, // Better open performance on mobile
        }}
        sx={{
          display: { xs: 'block', sm: 'none' },
          '& .MuiDrawer-paper': {
            width: 240,
            backgroundColor: 'rgba(26, 27, 30, 0.8)',
            backdropFilter: 'blur(10px)',
            boxSizing: 'border-box',
          },
        }}
      >
        <SidebarContent onClose={handleDrawerToggle} />
      </Drawer>
    </>
  );
}

Sidebar.propTypes = {
  mobileOpen: PropTypes.bool.isRequired,
  handleDrawerToggle: PropTypes.func.isRequired,
};

