export const EmailSettingDialog = ({ open, onClose }) => (
  <Dialog 
    open={open} 
    onClose={onClose}
    PaperProps={{
      style: {
        backgroundColor: 'rgba(26, 27, 30, 0.95)',
        backdropFilter: 'blur(10px)',
        border: '1px solid rgba(255, 255, 255, 0.1)',
      }
    }}
  >
    <DialogTitle sx={{ color: 'white' }}>Edit email</DialogTitle>

    <DialogContent>
      <Box sx={{ mt: 2 }}>
        <TextField
          fullWidth
          label="old email"
          type="email"
          sx={{
            mb: 2,
            '& .MuiOutlinedInput-root': {
              color: 'white',
              '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
              '&:hover fieldset': { borderColor: '#2DD4BF' },
              '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
            },
            '& .MuiInputLabel-root': {
              color: 'rgba(255, 255, 255, 0.7)',
              '&.Mui-focused': { color: '#2DD4BF' },
            },
          }}
        />

        <TextField
          fullWidth
          label="new email"
          type="email"
          InputLabelProps={{ shrink: true }}
          sx={{
            mb: 2,
            '& .MuiOutlinedInput-root': {
              color: 'white',
              '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
              '&:hover fieldset': { borderColor: '#2DD4BF' },
              '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
            },
            '& .MuiInputLabel-root': {
              color: 'rgba(255, 255, 255, 0.7)',
              '&.Mui-focused': { color: '#2DD4BF' },
            },
          }}
        />

      
      </Box>
    </DialogContent>

    <DialogActions>
      <Button onClick={onClose} sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Cancel</Button>

      <Button 
        onClick={onClose}
        sx={{
          color: '#2DD4BF',
          '&:hover': { backgroundColor: 'rgba(45, 212, 191, 0.1)' },
        }}
      >
        Comfirm
      </Button>
    </DialogActions>
  </Dialog>
);

EmailSettingDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
};

