
// Action Types
const LOGIN = 'LOGIN';
const LOGOUT = 'LOGOUT';
const isMobileDevice = 'IsMobileDevice';
// Initial State
const initialState = {
    user: {
        phoneNumber: '+1 (555) 123-4567',
        password: 'Last changed 3 months ago',
    },
    isAuthenticated: false,
    isMobileDevice: false, 
};

const authReducer = (state = initialState, action= {}) => {
    console.log('Action received:', action);

    switch (action.type) {
        case LOGIN:
            return { ...state, isAuthenticated: true };
        case LOGOUT:
            return { ...state, user: null, isAuthenticated: false };
        case  IsMobileDevice:
            return { ...state, isMobileDevice: action.payload.MobileDevice }; 
            default:
            return state;
    }
};

export const redux_login = () => ({
    type: LOGIN,
    // payload: user
});

export const redux_logout = () => ({
    type: LOGOUT,
});
export const IsMobileDevice = (MobileDevice) => ({
    type: isMobileDevice,
    payload: MobileDevice
});

export default authReducer;
