import { useState, useCallback, useEffect } from 'react';
import { usePlaidLink } from 'react-plaid-link';
import {
  Button,
  Typography,
  Alert,
  CircularProgress,
  Box,
  Card,
  CardContent
} from '@mui/material';
import PropTypes from 'prop-types';
import { BanknoteIcon as Bank } from 'lucide-react';
import { createLinkToken, exchangePublicToken } from '../../api/payment_n_transaction';

const PlaidBankConnection = ({ onSuccess }) => {
  const [status, setStatus] = useState('idle');
  const [error, setError] = useState(null);
  const [linkToken, setLinkToken] = useState(null);

  const generateLinkToken = useCallback(async () => {
    try {
      setStatus('loading');
      const data = await createLinkToken();
      console.log('Link token response:', data);


      if (!data.linkToken) {
        throw new Error('Link token not found in response');
      }

      setLinkToken(data.linkToken);
      setStatus('ready');
    } catch (err) {
      console.error('Link token error:', err);
      setError(err.message || 'Failed to initialize Plaid connection');
      setStatus('error');
    }
  }, []);

  useEffect(() => {
    generateLinkToken();
  }, [generateLinkToken]);

  const handlePlaidSuccess = useCallback(async (publicToken, metadata) => {
    try {
      setStatus('connecting');
      const data = await exchangePublicToken(publicToken);
      console.log('Exchange token response:', data);

      setStatus('connected');
      onSuccess(data);
    } catch (err) {
      console.error('Exchange token error:', err);
      setError(err.message || 'Failed to connect bank account');
      setStatus('error');
    }
  }, [onSuccess]);

  const handleRetry = () => {
    setError(null);
    setStatus('idle');
    setLinkToken(null);
    generateLinkToken();
  };

  const { open, ready } = usePlaidLink({
    token: linkToken ?? null,
    onSuccess: handlePlaidSuccess,
    onExit: () => {
      if (status !== 'connected') {
        setStatus('idle');
      }
    },
    onError: (err) => {
      console.error('Plaid Link error:', err);
      setError(err.message || 'Error connecting to Plaid');
      setStatus('error');
    }
  });

  const renderStatus = () => {
    switch (status) {
      case 'loading':
        return (
            <Box display="flex" alignItems="center" justifyContent="center" gap={2} mb={2}>
              <CircularProgress size={20} />

              <Typography sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Initializing Plaid connection...</Typography>
            </Box>
        );
      case 'error':
        return (
            <Alert
                severity="error"
                sx={{ mb: 2, backgroundColor: 'rgba(211, 47, 47, 0.1)', color: '#ef5350' }}
                action={
                  <Button color="inherit" size="small" onClick={handleRetry}>
                    Retry
                  </Button>
                }
            >
              {error}
            </Alert>
        );
      default:
        return null;
    }
  };

  return (
      <Card sx={{
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        border: '1px solid rgba(255, 255, 255, 0.1)',
        borderRadius: 2,
        height: '100%',
      }}>
        <CardContent sx={{ textAlign: 'center', py: 6 }}>
          <Typography variant="h4" sx={{
            color: 'white',
            mb: 4,
            fontWeight: 500
          }}>
            Connect Your Bank Account
          </Typography>

          {renderStatus()}

          {(status === 'idle' || status === 'ready') && linkToken && (
              <Button
                  variant="contained"
                  fullWidth
                  onClick={() => open()}
                  disabled={!ready}
                  startIcon={<Bank size={20} />}
                  sx={{
                    backgroundColor: '#5DDCB5',
                    color: '#1A1B1E',
                    '&:hover': {
                      backgroundColor: '#4DCBA4',
                    },
                    textTransform: 'none',
                    py: 2,
                    px: 4,
                    fontSize: '1.1rem',
                    minHeight: '56px',
                    maxWidth: '400px',
                    margin: '0 auto'
                  }}
              >
                Connect Bank Account
              </Button>
          )}

          <Typography
              variant="body2"
              sx={{ color: 'rgba(255, 255, 255, 0.7)', mt: 3, textAlign: 'center' }}
          >
            Securely connect your bank account using Plaid
          </Typography>
        </CardContent>
      </Card>
  );
};

PlaidBankConnection.propTypes = {
  onSuccess: PropTypes.func.isRequired,
};

export default PlaidBankConnection;

