import { useEffect } from 'react';
import { BottomNavigation, BottomNavigationAction, Paper } from '@mui/material';
import { useNavigate, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Home, CreditCard, DollarSign, Settings, Bell } from 'lucide-react';

const BottomNav = ({ routes }) => {
  const navigate = useNavigate();
  const location = useLocation();

  const isLoginPage = location.pathname === '/' || location.pathname === '/login'|| location.pathname === '/home';
   useEffect(() => {
    console.log('Location:', location.pathname);
   });

  const handleChange = (event, newValue) => {
    navigate(newValue);
  };


  const getNavConfig = (path) => {
    switch (path) {
      case '/dashboard':
        return { icon: <Home size={24} />, label: 'Home' };
      case '/dashboard/manage-cards':
        return { icon: <CreditCard size={24} />, label: 'Cards' };
      case '/dashboard/payments':
        return { icon: <DollarSign size={24} />, label: 'Payments' };
      case '/dashboard/notifications':
        return { icon: <Bell size={24} />, label: 'Notifications' };
      case '/dashboard/settings':
        return { icon: <Settings size={24} />, label: 'Settings' };
      default:
        return null;
    }
  };

  const navRoutes = [
    '/dashboard',
    '/dashboard/manage-cards',
    '/dashboard/payments',
    '/dashboard/notifications',
    '/settings'
  ];

  return (
    <Paper 
      sx={{ 
        position: 'fixed', 
        bottom: 0, 
        left: 0, 
        right: 0,
        zIndex: 1000,
        borderTop: '1px solid rgba(255, 255, 255, 0.12)',
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        display: isLoginPage ? 'none' : { xs: 'block', sm: 'none' } 
      }} 
      elevation={3}
    >
      <BottomNavigation
        value={location.pathname}
        onChange={handleChange}
        showLabels
        sx={{
          backgroundColor: 'transparent',
          '& .MuiBottomNavigationAction-root': {
            color: 'rgba(255, 255, 255, 0.7)',
            '&.Mui-selected': {
              color: '#2DD4BF',
            },
          },
        }}
      >
        {navRoutes.map((path) => {
          const config = getNavConfig(path);
          if (!config) return null;
          
          return (
            <BottomNavigationAction 
              key={path}
              label={config.label}
              value={path}
              icon={config.icon}
            />
          );
        })}
      </BottomNavigation>
    </Paper>
  );
};

BottomNav.propTypes = {
  routes: PropTypes.arrayOf(
    PropTypes.shape({
      path: PropTypes.string.isRequired,
      element: PropTypes.element.isRequired,
    })
  ).isRequired,
};

export default BottomNav;

