import { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import { Box, Container, Typography, Grid, Card, CardContent, Button, Dialog, DialogTitle, DialogContent, DialogActions, TextField } from '@mui/material';
import { CreditCard, Plus, Trash2, BanknoteIcon as Bank } from 'lucide-react';
import Sidebar from '../Dashboard/components/Sidebar';
import Header from '../Dashboard/components/Header';
import PlaidBankConnection from '../../components/payments/PlaidBankConnection';
import {getaccounts} from "../../api/payment_n_transaction";

const CardItem = ({ card, onDelete }) => (
    <Card sx={{
        backgroundColor: 'rgba(26, 27, 30, 0.8)',
        backdropFilter: 'blur(10px)',
        border: '1px solid rgba(255, 255, 255, 0.1)',
        borderRadius: 2,
    }}>
        <CardContent>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <CreditCard size={24} color="#2DD4BF" />

                <Button
                    variant="outlined"
                    size="small"
                    startIcon={<Trash2 size={16} />}
                    onClick={() => onDelete(card.id)}
                    sx={{
                        color: '#ef4444',
                        borderColor: '#ef4444',
                        '&:hover': {
                            backgroundColor: 'rgba(239, 68, 68, 0.1)',
                            borderColor: '#ef4444',
                        },
                    }}
                >
                    Delete
                </Button>
            </Box>

            <Typography variant="h6" sx={{ color: 'white', mb: 1 }}>
                **** **** **** {card.lastFour}
            </Typography>

            <Typography variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
                Expires: {card.expiryMonth}/

                {card.expiryYear}
            </Typography>
        </CardContent>
    </Card>
);

CardItem.propTypes = {
    card: PropTypes.shape({
        id: PropTypes.number.isRequired,
        lastFour: PropTypes.string.isRequired,
        expiryMonth: PropTypes.string.isRequired,
        expiryYear: PropTypes.string.isRequired,
    }).isRequired,
    onDelete: PropTypes.func.isRequired,
};

const BankAccountsSection = () => {
    const [bankAccounts, setBankAccounts] = useState([]);

    const handleBankConnectionSuccess = useCallback(async (data) => {
        console.log('Bank connected successfully:', data, data.accessToken);
        const accountsData = await getaccounts(data.accessToken);
        console.log('Accounts:', accountsData);

        if (accountsData && accountsData.accounts) {
            setBankAccounts(accountsData.accounts);
        }
    }, []);

    return (
        <>
            {bankAccounts.length > 0 ? (
                <>
                    {bankAccounts.map((account) => (
                        <Box key={account.accountId}>
                            <Card sx={{
                                backgroundColor: 'rgba(26, 27, 30, 0.8)',
                                backdropFilter: 'blur(10px)',
                                border: '1px solid rgba(255, 255, 255, 0.1)',
                                borderRadius: 2,
                                mb: 2,
                            }}>
                                <CardContent sx={{ p: 3 }}>
                                    <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 2 }}>
                                        <Bank size={24} style={{ color: '#2DD4BF', marginTop: 4 }} />

                                        <Box>
                                            <Typography variant="h6" sx={{
                                                color: 'white',
                                                fontSize: '1.1rem',
                                                fontWeight: 500,
                                                mb: 0.5
                                            }}>
                                                {account.name}

                                                (*{account.mask})
                                            </Typography>

                                            <Typography variant="body2" sx={{
                                                color: 'rgba(255, 255, 255, 0.7)',
                                                fontSize: '0.9rem'
                                            }}>
                                                {account.subtype.toUpperCase()} - Balance:

                                                {account.balances.current}

                                                {account.balances.isoCurrencyCode}
                                            </Typography>
                                        </Box>
                                    </Box>
                                </CardContent>
                            </Card>
                        </Box>
                    ))}
                </>
            ) : (
                <PlaidBankConnection onSuccess={handleBankConnectionSuccess} />
            )}
        </>
    );
};

const CardsPage = ({ mobileOpen, handleDrawerToggle, isFullscreen }) => {
    const [cards, setCards] = useState([
        { id: 1, lastFour: '1234', expiryMonth: '12', expiryYear: '24' },
        { id: 2, lastFour: '5678', expiryMonth: '06', expiryYear: '25' },
    ]);
    const [openDialog, setOpenDialog] = useState(false);
    const [newCard, setNewCard] = useState({ number: '', expiryMonth: '', expiryYear: '', cvv: '' });

    const handleDeleteCard = (id) => {
        setCards(cards.filter(card => card.id !== id));
    };

    const handleAddCard = () => {
        const lastFour = newCard.number.slice(-4);
        const newCardObj = {
            id: cards.length + 1,
            lastFour,
            expiryMonth: newCard.expiryMonth,
            expiryYear: newCard.expiryYear,
        };
        setCards([...cards, newCardObj]);
        setOpenDialog(false);
        setNewCard({ number: '', expiryMonth: '', expiryYear: '', cvv: '' });
    };

    return (
        <Box sx={{ display: 'flex' }}>
            <Sidebar mobileOpen={mobileOpen} handleDrawerToggle={handleDrawerToggle} />

            <Box
                component="main"
                sx={{
                    flexGrow: 1,
                    minHeight: '100vh',
                    backgroundColor: '#1A1B1E',
                    ml: { sm: '240px' },
                    width: { sm: `calc(100% - 240px)` },
                    py: 3,
                }}
            >
                <Container maxWidth="xl">
                    <Header handleDrawerToggle={handleDrawerToggle} />

                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
                        <Typography variant="h4" sx={{ color: 'white', flexGrow: 1 }}>
                            Manage Cards
                        </Typography>

                        <Button
                            variant="contained"
                            startIcon={<Plus size={20} />}
                            onClick={() => setOpenDialog(true)}
                            sx={{
                                backgroundColor: '#2DD4BF',
                                '&:hover': {
                                    backgroundColor: '#26b5a3',
                                },
                            }}
                        >
                            Add New Card
                        </Button>
                    </Box>

                    <Grid container spacing={3}>
                        {cards.map(card => (
                            <Grid item xs={12} sm={6} md={4} key={card.id}>
                                <CardItem card={card} onDelete={handleDeleteCard} />
                            </Grid>
                        ))}
                    </Grid>

                    <Typography variant="h4" sx={{ color: 'white', mt: 4, mb: 3 }}>
                        Bank Account
                    </Typography>

                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6} md={4}>
                            <BankAccountsSection />
                        </Grid>
                    </Grid>
                </Container>
            </Box>

            <Dialog
                open={openDialog}
                onClose={() => setOpenDialog(false)}
                PaperProps={{
                    sx: {
                        backgroundColor: 'rgba(26, 27, 30, 0.95)',
                        backdropFilter: 'blur(10px)',
                        border: '1px solid rgba(255, 255, 255, 0.1)',
                    }
                }}
            >
                <DialogTitle sx={{ color: 'white' }}>Add New Card</DialogTitle>

                <DialogContent>
                    <TextField
                        autoFocus
                        margin="dense"
                        label="Card Number"
                        type="text"
                        fullWidth
                        value={newCard.number}
                        onChange={(e) => setNewCard({ ...newCard, number: e.target.value })}
                        sx={{
                            mt: 2,
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                        }}
                    />

                    <TextField
                        margin="dense"
                        label="Expiry Month"
                        type="text"
                        fullWidth
                        value={newCard.expiryMonth}
                        onChange={(e) => setNewCard({ ...newCard, expiryMonth: e.target.value })}
                        sx={{
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                        }}
                    />

                    <TextField
                        margin="dense"
                        label="Expiry Year"
                        type="text"
                        fullWidth
                        value={newCard.expiryYear}
                        onChange={(e) => setNewCard({ ...newCard, expiryYear: e.target.value })}
                        sx={{
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                        }}
                    />

                    <TextField
                        margin="dense"
                        label="CVV"
                        type="text"
                        fullWidth
                        value={newCard.cvv}
                        onChange={(e) => setNewCard({ ...newCard, cvv: e.target.value })}
                        sx={{
                            '& .MuiOutlinedInput-root': {
                                color: 'white',
                                '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
                                '&:hover fieldset': { borderColor: '#2DD4BF' },
                                '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
                            },
                            '& .MuiInputLabel-root': {
                                color: 'rgba(255, 255, 255, 0.7)',
                                '&.Mui-focused': { color: '#2DD4BF' },
                            },
                        }}
                    />
                </DialogContent>

                <DialogActions>
                    <Button onClick={() => setOpenDialog(false)} sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
                        Cancel
                    </Button>

                    <Button onClick={handleAddCard} sx={{ color: '#2DD4BF' }}>
                        Add
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

CardsPage.propTypes = {
    mobileOpen: PropTypes.bool.isRequired,
    handleDrawerToggle: PropTypes.func.isRequired,
    isFullscreen: PropTypes.bool.isRequired,
};

export default CardsPage;

