import axios from "axios";

const SERVICE_BASE_URL =process.env.REACT_APP_NODE_ENV==='local' ?':8100': '/payment-n-transaction-service';

const payment_transaction_service = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL + SERVICE_BASE_URL,
  validateStatus: (status) => true,
});

export const getaccounts =async (accessToken)=>{
  try{
    const response=await payment_transaction_service.get(`/api/plaid/accounts?accessToken=${encodeURIComponent(accessToken)}`);

    if(response.status !== 200){
      throw new Error(`HTTP error! status:${response.status}`)
    }

    return response.data;
  }catch (error){
    console.error('Get Accounts error:',error)
  }
};

export const createLinkToken = async () => {
  try {
    const response = await payment_transaction_service.get('/api/plaid/create-link-token');

    if (response.status !== 200) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return response.data;
  } catch (error) {
    console.error('Create link token error:', error);
    throw error;
  }
};

export const exchangePublicToken = async (publicToken) => {
  try {
    const response = await payment_transaction_service.post('/api/plaid/exchange-token', { publicToken });

    if (response.status !== 200) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return response.data;
  } catch (error) {
    console.error('Exchange token error:', error);
    throw error;
  }
};

