import { useState } from 'react';
import PropTypes from 'prop-types';
import {
  Box,
  Typography,
  IconButton,
  Avatar,
  Button,
  Menu,
  MenuItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Badge,
} from '@mui/material';
import { WhatsApp } from '@mui/icons-material';
import { Bell, Settings, MessageCircle, HelpCircle, LogOut, MenuIcon } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

export default function Header({ handleDrawerToggle }) {
  const navigate = useNavigate();
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  // 假设有3个未读通知
  const unreadNotifications = 3;

  return (
    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
      <Box sx={{ display: 'flex', alignItems: 'center' }}>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          edge="start"
          onClick={handleDrawerToggle}
          sx={{ mr: 2, display: { sm: 'none' } }}
        >
          <MenuIcon />
        </IconButton>

        <Typography
          variant="h4"
          sx={{
            color: '#2DD4BF',
            fontWeight: 700,
            letterSpacing: '-0.02em',
          }}
        >
          Dashboard
        </Typography>
      </Box>

      <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
        <Button
          variant="contained"
          startIcon={<WhatsApp style={{ color: "green" }} />}
          sx={{
            backgroundColor: '#2DD4BF',
            '&:hover': {
              backgroundColor: '#26b5a3',
            },
            textTransform: 'none',
            px: 3,
            display: { xs: 'none', md: 'flex' },
          }}
          href="whatsapp://send?text=Discover Rife, an amazing way to make payments! https://rifeapp.github.io/io/ "
        >
          Share via WhatsApp
        </Button>

        <IconButton
          onClick={() => navigate('/dashboard/notifications')}
          sx={{
            backgroundColor: 'rgba(255, 255, 255, 0.05)',
            '&:hover': { backgroundColor: 'rgba(255, 255, 255, 0.1)' },
          }}
        >
          <Badge
            badgeContent={unreadNotifications}
            color="error"
            sx={{
              '& .MuiBadge-badge': {
                backgroundColor: '#ef4444',
                color: 'white',
              }
            }}
          >
            <Bell size={20} color="white" />
          </Badge>
        </IconButton>

        <Avatar
          onClick={handleClick}
          aria-controls={open ? 'account-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          sx={{
            bgcolor: '#2DD4BF',
            width: 32,
            height: 32,
            fontSize: '0.875rem',
            cursor: 'pointer',
            '&:hover': {
              opacity: 0.8,
            },
          }}
        >
          JD
        </Avatar>

        <Menu
          id="account-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          onClick={handleClose}
          PaperProps={{
            elevation: 0,
            sx: {
              overflow: 'visible',
              filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
              mt: 1.5,
              backgroundColor: 'rgba(26, 27, 30, 0.95)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255, 255, 255, 0.1)',
              borderRadius: 2,
              minWidth: 200,
              '& .MuiMenuItem-root': {
                py: 1.5,
                px: 2,
                color: 'white',
                '&:hover': {
                  backgroundColor: 'rgba(45, 212, 191, 0.1)',
                },
              },
            },
          }}
          transformOrigin={{ horizontal: 'right', vertical: 'top' }}
          anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
        >
          <MenuItem onClick={() => navigate('/dashboard/settings')}>
            <ListItemIcon>
                  <Settings size={18} color="#2DD4BF" />
            </ListItemIcon>

            <ListItemText>Settings</ListItemText>
          </MenuItem>

          <MenuItem onClick={() => navigate('/dashboard/support')}>
            <ListItemIcon>
              <MessageCircle size={18} color="#2DD4BF" />
            </ListItemIcon>

            <ListItemText>Support</ListItemText>
          </MenuItem>

          <MenuItem onClick={() => navigate('/dashboard/help-center')}>
            <ListItemIcon>
              <HelpCircle size={18} color="#2DD4BF" />
            </ListItemIcon>

            <ListItemText>Help Center</ListItemText>
          </MenuItem>

          <Divider sx={{ borderColor: 'rgba(255, 255, 255, 0.1)' }} />

          <MenuItem onClick={() => navigate('/logout')}>
            <ListItemIcon>
              <LogOut size={18} color="#ef4444" />
            </ListItemIcon>

            <ListItemText sx={{ color: '#ef4444' }}>Sign out</ListItemText>
          </MenuItem>
        </Menu>
      </Box>
    </Box>
  );
}

Header.propTypes = {
  handleDrawerToggle: PropTypes.func.isRequired,
};

