import axios from 'axios';
const SERVICE_BASE_URL='/user-account-service/api';

const user_account_service = axios.create({
    baseURL: process.env.REACT_APP_BASE_URL+SERVICE_BASE_URL,
    validateStatus: (status) => true,
  });
  

export  const  verify_phone=async(phoneNumber)=>{
    console.log(process.env.REACT_APP_NODE_ENV)

    try{
        alert(JSON.stringify(phoneNumber)+'/auth/verify-phone')
        const response =await user_account_service.post('/auth/verify-phone',JSON.stringify(phoneNumber), 
             {
            headers: {
              'Content-Type': 'application/json',
            }})
    }catch(error){
        console.error('ERROR sending phone number:',error);
        throw error;
    }
}

export const login=async(loginPayLoad)=>{
  try{
    const response=await user_account_service.post('/auth/login',JSON.stringify(loginPayLoad),{
      headers:{
        'Content-Type':'application/json',
      }
    })
    return response
  }catch(error){
    console.error('Error in login request:',error);
    throw error;
  }


}