import { useState } from 'react';
import PropTypes from 'prop-types';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Checkbox,
  Avatar,
  Typography,
} from '@mui/material';

const CreateGroupDialog = ({ open, onClose, payees, fullWidth, maxWidth }) => {
  const [groupName, setGroupName] = useState('');
  const [selectedPayees, setSelectedPayees] = useState([]);

  const handleToggle = (payeeId) => {
    const currentIndex = selectedPayees.indexOf(payeeId);
    const newSelectedPayees = [...selectedPayees];

    if (currentIndex === -1) {
      newSelectedPayees.push(payeeId);
    } else {
      newSelectedPayees.splice(currentIndex, 1);
    }

    setSelectedPayees(newSelectedPayees);
  };

  const handleCreateGroup = () => {
    console.log('Group created:', { name: groupName, members: selectedPayees });
    // Here you would typically make an API call to create the group
    setGroupName('');
    setSelectedPayees([]);
    onClose();
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      fullWidth
      maxWidth="sm"
      PaperProps={{
        style: {
          backgroundColor: 'rgba(26, 27, 30, 0.95)',
          backdropFilter: 'blur(10px)',
          border: '1px solid rgba(255, 255, 255, 0.1)',
        }
      }}
    >
      <DialogTitle sx={{ color: 'white' }}>Create Group</DialogTitle>

      <DialogContent>
        <TextField
          autoFocus
          margin="dense"
          label="Group Name"
          type="text"
          fullWidth
          value={groupName}
          onChange={(e) => setGroupName(e.target.value)}
          sx={{
            mt: 2,
            mb: 2,
            '& .MuiOutlinedInput-root': {
              color: 'white',
              '& fieldset': { borderColor: 'rgba(255, 255, 255, 0.1)' },
              '&:hover fieldset': { borderColor: '#2DD4BF' },
              '&.Mui-focused fieldset': { borderColor: '#2DD4BF' },
            },
            '& .MuiInputLabel-root': {
              color: 'rgba(255, 255, 255, 0.7)',
              '&.Mui-focused': { color: '#2DD4BF' },
            },
          }}
        />

        <Typography variant="subtitle2" sx={{ color: 'white', mt: 2, mb: 1 }}>
          Select Group Members
        </Typography>

        <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'transparent' }}>
          {payees.map((payee) => (
            <ListItem
              key={payee.id}
              dense
              button
              onClick={() => handleToggle(payee.id)}
            >
              <ListItemIcon>
                <Checkbox
                  edge="start"
                  checked={selectedPayees.indexOf(payee.id) !== -1}
                  tabIndex={-1}
                  disableRipple
                  sx={{
                    color: 'rgba(255, 255, 255, 0.7)',
                    '&.Mui-checked': {
                      color: '#2DD4BF',
                    },
                  }}
                />
              </ListItemIcon>

              <ListItemIcon>
                <Avatar sx={{ bgcolor: '#2DD4BF' }}>{payee.name[0]}</Avatar>
              </ListItemIcon>

              <ListItemText 
                primary={payee.name} 
                secondary={payee.type}
                sx={{ 
                  '& .MuiListItemText-primary': { color: 'white' },
                  '& .MuiListItemText-secondary': { color: 'rgba(255, 255, 255, 0.7)' },
                }}
              />
            </ListItem>
          ))}
        </List>
      </DialogContent>

      <DialogActions>
        <Button onClick={onClose} sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>Cancel</Button>

        <Button 
          onClick={handleCreateGroup} 
          disabled={!groupName || selectedPayees.length === 0}
          sx={{ 
            color: '#2DD4BF',
            '&.Mui-disabled': {
              color: 'rgba(255, 255, 255, 0.3)',
            },
          }}
        >
          Create Group
        </Button>
      </DialogActions>
    </Dialog>
  );
};

CreateGroupDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  payees: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
    type: PropTypes.string,
  })).isRequired,
  fullWidth: PropTypes.bool,
  maxWidth: PropTypes.string,
};

export default CreateGroupDialog;

