import React from 'react';
import PropTypes from 'prop-types';
import { Box, Container, Typography, Card, CardContent, List, ListItem, ListItemText, Divider, IconButton } from '@mui/material';
import { Bell, CreditCard, DollarSign, UserPlus, Menu } from 'lucide-react';
import Sidebar from '../Dashboard/components/Sidebar';

const notifications = [
  {
    id: 1,
    title: "Payment Received",
    message: "You received $50.00 from John Doe",
    time: "2 minutes ago",
    type: "payment",
    isRead: false
  },
  {
    id: 2,
    title: "New Card Added",
    message: "Your card ending in 4242 has been successfully added",
    time: "1 hour ago",
    type: "card",
    isRead: false
  },
  {
    id: 3,
    title: "Friend Request",
    message: "Jane Smith wants to connect with you",
    time: "3 hours ago",
    type: "friend",
    isRead: false
  },
  {
    id: 4,
    title: "Payment Sent",
    message: "Payment of $30.00 to Alice Johnson was successful",
    time: "1 day ago",
    type: "payment",
    isRead: true
  }
];

const NotificationIcon = ({ type }) => {
  const icons = {
    payment: DollarSign,
    card: CreditCard,
    friend: UserPlus,
  };
  const Icon = icons[type] || Bell;
  
  return (
    <Box
      sx={{
        width: 40,
        height: 40,
        borderRadius: '50%',
        backgroundColor: 'rgba(45, 212, 191, 0.1)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        mr: 2
      }}
    >
      <Icon size={20} color="#2DD4BF" />
    </Box>
  );
};

NotificationIcon.propTypes = {
  type: PropTypes.oneOf(['payment', 'card', 'friend']).isRequired,
};

const NotificationsPage = ({ mobileOpen, handleDrawerToggle, isFullscreen }) => {
  return (
    <Box sx={{ display: 'flex' }}>
      <Sidebar mobileOpen={mobileOpen} handleDrawerToggle={handleDrawerToggle} />

      <Box
        component="main"
        sx={{
          flexGrow: 1,
          minHeight: '100vh',
          backgroundColor: '#1A1B1E',
          ml: { sm: '240px' },
          width: { sm: `calc(100% - 240px)` },
          py: 3,
        }}
      >
        <Container maxWidth="xl">
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 4 }}>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { sm: 'none' } }}
            >
              <Menu />
            </IconButton>

            <Typography variant="h4" sx={{ color: 'white' }}>
              Notifications
            </Typography>
          </Box>

          <Card
            sx={{
              backgroundColor: 'rgba(26, 27, 30, 0.8)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255, 255, 255, 0.1)',
              borderRadius: 2,
            }}
          >
            <CardContent>
              <List sx={{ py: 0 }}>
                {notifications.map((notification, index) => (
                  <React.Fragment key={notification.id}>
                    <ListItem
                      sx={{
                        py: 2,
                        px: 3,
                        backgroundColor: notification.isRead ? 'transparent' : 'rgba(45, 212, 191, 0.05)',
                        '&:hover': {
                          backgroundColor: 'rgba(45, 212, 191, 0.1)',
                        },
                      }}
                    >
                      <NotificationIcon type={notification.type} />

                      <ListItemText
                        primary={
                          <Typography
                            variant="subtitle1"
                            sx={{
                              color: 'white',
                              fontWeight: notification.isRead ? 400 : 600,
                              mb: 0.5,
                            }}
                          >
                            {notification.title}
                          </Typography>
                        }
                        secondary={
                          <Box>
                            <Typography
                              variant="body2"
                              sx={{
                                color: 'rgba(255, 255, 255, 0.7)',
                                mb: 0.5,
                              }}
                            >
                              {notification.message}
                            </Typography>

                            <Typography
                              variant="caption"
                              sx={{
                                color: 'rgba(255, 255, 255, 0.5)',
                              }}
                            >
                              {notification.time}
                            </Typography>
                          </Box>
                        }
                      />
                    </ListItem>

                    {index < notifications.length - 1 && (
                      <Divider sx={{ backgroundColor: 'rgba(255, 255, 255, 0.1)' }} />
                    )}
                  </React.Fragment>
                ))}
              </List>
            </CardContent>
          </Card>
        </Container>
      </Box>
    </Box>
  );
};

NotificationsPage.propTypes = {
  mobileOpen: PropTypes.bool.isRequired,
  handleDrawerToggle: PropTypes.func.isRequired,
  isFullscreen: PropTypes.bool.isRequired,
};

export default NotificationsPage;

