
export const initGoogleAuth = (clientId, onSuccess) => {


    const handleGoogleLogin = () => {
      const client = window.google?.accounts?.oauth2?.initCodeClient({
        client_id: clientId,
        scope: "email profile",
        redirect_uri: window.location.origin + "/io",
        callback: (response) => {
          if (response.code) {
            onSuccess(response);
          }
        },
      });

      client?.requestCode();
    };
  
    const loadGoogleScript = () => {
      const script = document.createElement("script");
      script.src = "https://accounts.google.com/gsi/client";
      script.async = true;
      script.defer = true;
      document.body.appendChild(script);

      return () => {
        // document.body.removeChild(script);
      };
    };
  
    return { handleGoogleLogin, loadGoogleScript };
  };
  
  