import React, { useState, useCallback } from "react";
import { ThemeProvider, CssBaseline, Box } from "@mui/material";
import theme from "./components/theme/theme";
import { BrowserRouter, Routes, Route, useLocation } from "react-router-dom";
import { Provider } from "react-redux";
import { store, persistor } from "./store";
import { PersistGate } from "redux-persist/integration/react";
import useFullscreenOnMobile from "./hooks/useFullscreenOnMobile";
import BottomNav from "./components/BottomNav";
import routes from "./routes";
import ScrollToTop from "./components/ScrollToTop";

function AppContent() {
  const location = useLocation();
  const [mobileOpen, setMobileOpen] = useState(false);
  const isFullscreen = useFullscreenOnMobile();

  const handleDrawerToggle = useCallback(() => {
    setMobileOpen((prevState) => !prevState);
  }, []);

  return (
    <Box sx={{ pb: 7 }}>
      <ScrollToTop />

      <Routes>
        {routes.map((route, index) => (
          <Route
            key={index}
            path={route.path}
            element={React.cloneElement(route.element, {
              mobileOpen,
              handleDrawerToggle,
              isFullscreen,
       })}
          >
            {route.children &&
              route.children.map((childRoute, childIndex) => (
                <Route
                  key={childIndex}
                  path={childRoute.path}
                  element={React.cloneElement(childRoute.element, {
                    mobileOpen,
                    handleDrawerToggle,
                    isFullscreen,
                  })}
                />
              ))}
          </Route>
        ))}
      </Routes>

      <BottomNav routes={routes} />
    </Box>
  );
}

function App() {
  return (
    <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
        <ThemeProvider theme={theme}>
          <CssBaseline />

         <BrowserRouter basename="/io">
            <AppContent />
          </BrowserRouter>
        </ThemeProvider>
      </PersistGate>
    </Provider>
  );
}

export default App;
